import { ngon, translate, Cubic, multiply, add, XY } from "../../geometry";

export const useNgon = (sides: number, scale:number, rotation: number, cornerRadius: number, strokeWidth: number): [number[][], Cubic, number] => {
	const ssw = strokeWidth * scale;
	const scr = cornerRadius * scale;
	
	const radius = scale/2;
	const [cpts, vb] = ngon(sides, radius, rotation, scr, [0, 0]);
	const hsw = ssw/2;
	const bvb: Cubic = add(vb, [-hsw, -hsw, ssw, ssw]) as Cubic;
	const tvp: XY = [-bvb[0], -bvb[1]] as XY;
	const points = translate(cpts, [bvb[0]])
	console.log(`Generating a polygon with the following dimensions
sides: ........... ${sides}
scale: ........... ${scale}
strokeWidth: ..... ${strokeWidth}
scaledStrokeWidth: ${ssw}
bounds: .......... ${bvb}

	`);
	return [cpts, viewBox, ssw];
}